.game {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
}

.board-row {
    display: flex;
    justify-content: center;
}

.square {
    border: 2px solid darkorange;
    font-size: 5rem;
    height: 25vw;
    width: 25vw;
    max-height: 100px;
    max-width: 100px;
    margin: 0.05vw;
    border-radius: 15px;
    padding: 0;
    text-align: center;
    box-shadow: 3px 5px 10px rgba(0, 0, 0, 0.1);
}

.square:hover {
    background-color: #b4b3b3;
    color: white;
}

.status {
    margin-bottom: 3vh;
    width: 96%;
    height: auto;
    background-color: #61dafb;
    border-radius: 12px;
    box-shadow: 3px 5px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    padding: 10px;
}

.chose {
    margin-bottom: 5vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;

}

.chose1,
.chose2 {
    width: 100%;
    max-width: 300px;
    height: auto;
    background-color: #e0be93;
    border-radius: 12px;
    box-shadow: 3px 5px 10px rgba(0, 0, 0, 0.1);
    text-align: center;
    padding: 1rem;
    cursor: pointer;

}

@media (min-width: 600px) {
    .square {
        font-size: 4vw;
        height: 20vw;
        width: 20vw;
        max-height: 150px;
        max-width: 150px;
    }

    .chose {
        gap: 2vw;
    }
}

@media (min-width: 768px) {
    .square {
        font-size: 3vw;
        height: 15vw;
        width: 15vw;
        max-height: 200px;
        max-width: 200px;
    }
}

@media (min-width: 992px) {
    .square {
        font-size: 2vw;
        height: 10vw;
        width: 10vw;
        max-height: 250px;
        max-width: 250px;
    }

    .chose {
        gap: 4vw;

    }
}

@media (min-width: 1200px) {
    .square {
        font-size: 4vw;
        height: 8vw;
        width: 8vw;
        max-height: 300px;
        max-width: 300px;
    }
    .chose {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 1rem;
    }

}